export const fetchApi = async (query) => {
    try {
      const response = await fetch('http://localhost:8080/api/query', {
        method: 'POST',
        headers: {
          'Content-Type': 'text/plain',
        },
        body: query,
        mode: 'cors',
      });
  
      if (response.ok) {
        const result = await response.json();
        return result;
      } else {
        const error = await response.text();
        console.error(`API Error: ${error}`);
        return null;
      }
    } catch (error) {
    //   console.error(`Request failed: ${error.message}`);
      return null;
    }
  };

export const createInputField = (type, id, name, required = false, className = '') => {
    return (
        <input 
            type={type} 
            id={id} 
            name={name} 
            required={required} 
            className={className}
        />
    );
};

export const createTextArea = (id, name, className = '') => {
    return (
        <textarea 
            id={id} 
            name={name} 
            className={className}
        />
    );
};