import React, { useState, useEffect } from 'react';
import './Components.css';
import logo from '../assets/images/zetaris-logo.png';
import '../styleguides/styleguides.css';

function Header({ setView }) {
  const [selectedTab, setSelectedTab] = useState('');

  useEffect(() => {
    handleClick('sqlEditor');
  }, []);

  const handleClick = (viewName) => {
    setSelectedTab(viewName);
    setView(viewName);
  };

  return (
    <div className="header-container">
      <div className="logo">
        <img src={logo} alt="Logo" />
        <span className="logo_text">Zetaris</span>
      </div>
      <div className="menu-tabs">
        <button
          className={`btn-header bold-text ${selectedTab === 'sqlEditor' ? 'selected' : ''}`}
          onClick={() => handleClick('sqlEditor')}
        >
          SQL Editor
        </button>
        <button
          className={`btn-header bold-text ${selectedTab === 'semanticLayer' ? 'selected' : ''}`}
          onClick={() => handleClick('semanticLayer')}
        >
          Semantic Layer
        </button>
        <button
          className={`btn-header bold-text ${selectedTab === 'documentation' ? 'selected' : ''}`}
          onClick={() => handleClick('documentation')}
        >
          Documentation
        </button>
        <button
          className={`btn-header bold-text ${selectedTab === 'styleguide' ? 'selected' : ''}`}
          onClick={() => handleClick('styleguide')}
        >
          Style Guide
        </button>
      </div>
    </div>
  );
}

export default Header;