import React, { useState, useRef, useLayoutEffect, useEffect } from 'react';
import AceEditor from 'react-ace';
import Resizable from 'react-resizable-layout';
import { initializeJsPlumb, setupTable, setupTableForSelectedTable } from './configs/JsPlumbConfig';
import StyleGuide from '../styleguides/styleguides';
import { v4 as uuidv4 } from 'uuid'; // add UUID library
import { fetchApi } from '../utils/common';

// Import editor configuration
import { defineCustomTheme, setupAceEditor, editorOptions } from './configs/editorConfig';

import 'ace-builds/src-noconflict/mode-sql';
import 'ace-builds/src-noconflict/theme-monokai';
import 'ace-builds/src-noconflict/ext-language_tools';

import { ReactComponent as XmarkIcon } from '../assets/images/xmark-solid.svg';

function Content({ view, toggleRefreshNav, selectedTable }) {
  const jsPlumbRef = useRef(null);
  const [editors, setEditors] = useState([{ id: 1, name: 'Editor 1', content: '' }]);
  const [activeEditor, setActiveEditor] = useState(1);
  const [tables, setTables] = useState([]);
  const [queryResult, setQueryResult] = useState('Results will be displayed here.');
  const jsPlumbInstanceRef = useRef(null);
  
  // Load editor configurations once the component is mounted
  useEffect(() => {
    defineCustomTheme();
    setupAceEditor();
  }, []);

  // Load the editor content from localStorage when the component is mounted
  useEffect(() => {
    const storedContent = localStorage.getItem('editorContent');
    if (storedContent) {
      setEditors([{ id: 1, name: 'Editor 1', content: storedContent }]);
    }
  }, []);

  // Save editor content to localStorage whenever the content changes
  useEffect(() => {
    const activeEditorContent = editors.find((editor) => editor.id === activeEditor)?.content;
    if (activeEditorContent) {
      localStorage.setItem('editorContent', activeEditorContent);
    }
  }, [editors, activeEditor]);

  useLayoutEffect(() => {
    if (view === 'semanticLayer' && jsPlumbRef.current && !jsPlumbInstanceRef.current) {
      jsPlumbInstanceRef.current = initializeJsPlumb(jsPlumbRef.current, tables);

      return () => {
        if (jsPlumbInstanceRef.current) {
          jsPlumbInstanceRef.current.reset(); // Reset the jsPlumb instance when the component unmounts
        }
      };
    }
  }, [view]);

  // useLayoutEffect(() => {
  //   if (jsPlumbInstanceRef.current) {
  //     jsPlumbInstanceRef.current.repaintEverything(); // Repaint after tables are updated
  //   }
  // }, [tables]);

  // Watch for changes in the selectedTable prop
  useEffect(() => {
    if (selectedTable && jsPlumbInstanceRef.current) {
      // console.log(selectedTable)
      const newTable = {
        id: `table-${uuidv4()}`,  // Unique ID for each table
        title: selectedTable.name,  // Table name
        top: `${50 + tables.length * 100}px`,  // Position table dynamically
        left: '50px',
        columns: selectedTable.desc.map(col => ({ name: col.name, type: col.type }))  // Column information
      };

      setTables(prevTables => [...prevTables, newTable]);  // Add new table to stat

      // Set up the table with description in jsPlumb
      requestAnimationFrame(() => {
        setupTableForSelectedTable(jsPlumbRef.current, selectedTable, jsPlumbInstanceRef.current);
      });
    }
  }, [selectedTable]);  // Trigger when selectedTable changes

  // const addTable = () => {
  //   const newTable = {
  //     id: `table-${uuidv4()}`, // Unique ID for each table
  //     title: `Table ${tables.length + 1}`,
  //     top: `${50 + tables.length * 100}px`,
  //     left: '50px',
  //   };
  //   setTables((prevTables) => [...prevTables, newTable]);

  //   if (jsPlumbInstanceRef.current) {
  //     // Setting drag after DOM element is fully rendered
  //     requestAnimationFrame(() => {
  //       setupTable(jsPlumbRef.current, newTable, jsPlumbInstanceRef.current);
  //     });
  //   }
  // };

  const addEditor = () => {
    const newId = editors.length + 1;
    setEditors([...editors, { id: newId, name: `Editor ${newId}`, content: '' }]);
    setActiveEditor(newId);
  };

  const handleEditorChange = (newValue, id) => {
    setEditors((prevEditors) =>
      prevEditors.map((editor) =>
        editor.id === id ? { ...editor, content: newValue } : editor
      )
    );
  };

  const switchEditor = (id) => {
    setActiveEditor(id);
  };

  const deleteEditor = (id) => {
    const updatedEditors = editors
      .filter((editor) => editor.id !== id)
      .map((editor, index) => ({
        ...editor,
        id: index + 1,
        name: `Editor ${index + 1}`,
      }));

    setEditors(updatedEditors);

    if (updatedEditors.length > 0) {
      const nextEditorId = id > updatedEditors.length ? updatedEditors.length : id;
      setActiveEditor(nextEditorId);
    } else {
      setActiveEditor(null); // No editors left
    }
  };

  // Render function to display the joined data in a table format
  const RenderTable = ({ data }) => {
    if (!data || data.length === 0) {
      return <div>No data available</div>;
    }
  
    // Extract table headers dynamically from the first object in the data array
    const headers = Object.keys(data[0]);
  
    return (
      <table border="1" cellPadding="10" style={{ borderCollapse: 'collapse', width: '100%' }}>
        <thead>
          <tr>
            {headers.map(header => (
              <th key={header}>{header}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {data.map((row, index) => (
            <tr key={index}>
              {headers.map(header => (
                <td key={header}>{row[header] !== undefined ? row[header].toString() : ''}</td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    );
  };

  const removeComments = (query) => {
    // Split the query by line breaks to process each line individually
    const lines = query.split('\n');
  
    // Filter out lines that start with -- (SQL comments)
    const cleanLines = lines.filter(line => !line.trim().startsWith('--'));
  
    // Join the clean lines back together
    return cleanLines.join('\n').trim();
  };
  

  // Updated runQuery function to fetch and display dynamic table results
  const runQuery = async () => {
    const activeEditorContent = editors.find((editor) => editor.id === activeEditor)?.content;

    if (!activeEditorContent) {
      setQueryResult({ error: 'No SQL query to run.' });
      return;
    }

    const cleanQuery = removeComments(activeEditorContent);

    const result = await fetchApi(cleanQuery); // Call Api methond in common.js

    if (cleanQuery.startsWith('CREATE') || cleanQuery.startsWith('DROP') || cleanQuery.startsWith('ALTER') || cleanQuery.startsWith('REGISTER')
      || cleanQuery.startsWith('REPLACE')) {
      toggleRefreshNav(); // Trigger the refresh
    }

    if (result) {
      // Handle stringified JSON objects in the array
      const parsedResult = result.map((item) => JSON.parse(item));

      // Check if the data has tables and is valid for display
      if (Array.isArray(parsedResult) && parsedResult.length > 0) {
        setQueryResult(<RenderTable data={parsedResult} />); // Display the result in a table
      } else {
        setQueryResult({ error: "Invalid data format or insufficient tables" });
      }
    } else {
      setQueryResult({ error: 'Failed to run query.' });
    }
  };

  const renderTable = () => {
    if (!queryResult) {
      return 'Results will be displayed here.';
    }

    if (queryResult.error) {
      return queryResult.error;
    }

    return queryResult;
  };

  const offset = 90; // offset for separator

  return (
    <div style={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
      {view === 'sqlEditor' && (
        <Resizable axis={'y'} initial={700} min={400} max={900}>
          {({ position, separatorProps }) => (
            <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
              <div className="editor-tabs">
                {editors.map((editor) => (
                  <div key={editor.id} style={{ display: 'inline-block' }}>
                    <button
                      className={`btn-editor ${activeEditor === editor.id ? 'selected' : ''}`}
                      onClick={() => switchEditor(editor.id)}
                    >
                      <span className="btn-editor-label">
                        {editor.name}
                        <div
                          className="btn-editor-close"
                          onClick={(e) => {
                            e.stopPropagation();
                            deleteEditor(editor.id);
                          }}
                        >
                          <XmarkIcon className="editor-xmark" style={{ fill: activeEditor === editor.id ? 'white' : '#27A7D2' }} />
                        </div>
                      </span>
                    </button>
                  </div>
                ))}
                <button className="btn-primary" onClick={addEditor}>
                  Add
                </button>
              </div>
              <div className="seperator-content"></div>
              <div style={{ height: position - offset }}>
                {editors.map(
                  (editor) =>
                    activeEditor === editor.id && (
                      <AceEditor
                        key={editor.id}
                        mode="sql"
                        theme="myCustomTheme"
                        name={`ace-editor-${editor.id}`}
                        fontSize={12}
                        showPrintMargin={true}
                        showGutter={true}
                        highlightActiveLine={true}
                        value={editor.content}
                        onChange={(newValue) => handleEditorChange(newValue, editor.id)}
                        setOptions={editorOptions}
                        style={{ width: '100%', height: '100%' }}
                      />
                    )
                )}
              </div>
              <button className="btn-primary" onClick={runQuery} style={{ marginTop: '10px' }}>
                Run
              </button>
              <div
                {...separatorProps}
                style={{
                  height: '3px',
                  backgroundColor: '#ccc',
                  cursor: 'row-resize',
                  overflowX: 'auto',
                }}
              />
              <div
                style={{
                  flexGrow: 1,
                  padding: '10px',
                  backgroundColor: '#f0f0f0',
                  overflowY: 'auto',
                  height: `calc(100% - ${position - offset}px)`,
                }}
              >
                {renderTable()}
              </div>
            </div>
          )}
        </Resizable>
      )}
      {view === 'semanticLayer' && (
        <Resizable axis={'y'} initial={700} min={400} max={900}>
          {({ position, separatorProps }) => {
            return (
              <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
                <div ref={jsPlumbRef} style={{ height: `${position - offset}px`, width: '100%', position: 'relative' }}>
                  {/* Table and connection points will be added here */}
                </div>

                {/* Separator line */}
                <div
                  {...separatorProps}
                  style={{
                    height: '3px',
                    backgroundColor: '#ccc',
                    cursor: 'row-resize',
                  }}
                />

                {/* Results area */}
                <div
                  style={{
                    flexGrow: 1,
                    padding: '10px',
                    backgroundColor: '#f0f0f0',
                    overflowY: 'auto',
                    height: `calc(100% - ${position - offset}px)`,
                  }}
                >
                  Results will be displayed here.
                </div>
              </div>
            );
          }}
        </Resizable>
      )}
      {view === 'documentation' && window.open('https://github.com/zetaris/lightning-catalog/tree/master/doc', '_blank')}
      {view === 'styleguide' && <StyleGuide />}
    </div>
  );
}

export default Content;
