import React, { useState, useEffect } from 'react';
import { SimpleTreeView } from '@mui/x-tree-view/SimpleTreeView';
import { TreeItem } from '@mui/x-tree-view/TreeItem';
import { fetchApi } from '../utils/common';
import './Navigation.css';
import '../styleguides/styleguides.css';

const Navigation = ( {view, onTableSelect, refreshNav} ) => {

  // State to hold the data source tree structure
  const [dataSources, setDataSources] = useState([
    {
      name: 'lightning.datasource',
      children: null // Initially, child nodes are empty
    }
  ]);

  // Function to get the depth (level) of the current path
  const getCurrentLevel = (fullPath) => {
    // Split the path by '.' and return the length of the resulting array
    const parts = fullPath.split('.');
    return parts.length; // This will give the depth (level) of the path
  };

  // Function to fetch child nodes from the API, keeping track of the full namespace path
  const fetchDatasources = async (fullPath) => {
    // Ensure that fullPath contains the full namespace (including 'lightning.datasource')
    let query;
    const currentLevel = getCurrentLevel(fullPath);

    // Check if the node is a table, if so, use a SELECT statement instead of SHOW NAMESPACES
    if (currentLevel === 5) {
      query = `SHOW TABLES IN ${fullPath}`;
    } else if(currentLevel === 6){
      query = `DESC ${fullPath}`;
    } else {
      query = `SHOW NAMESPACES IN ${fullPath};`; // Default query for non-table nodes
    }
    
    let result = await fetchApi(query); // Call the common API method

    if(!result || result.length === 0){
      // handle empty or undefined results
      if(query.startsWith('SHOW NAMESPACES')){
        query = `SHOW TABLES IN ${fullPath}`;
      }else if(query.startsWith('SHOW TABLES')){
        query = `DESC ${fullPath}`;
      }

      result = await fetchApi(query);
    }

    if (result) { 
      if (query.startsWith('SHOW TABLES')) {
          const parsedResult = result.map((item) => {
          const parsedItem = JSON.parse(item); // Parse each string into an object
          return parsedItem.tableName;
        });

        const excludedNamespaces = ["information_schema", "pg_catalog", "public"];

        // Check if the current level is 5 and filter excluded namespaces
        for (let namespace of parsedResult) {
          if (excludedNamespaces.includes(namespace)) {
            return [];
          }
        }

        return parsedResult.map((table) => ({
          name: table,
          fullPath: `${fullPath}.${table}`,
          isTable: true,
          children: null
        }));

      } else if(query.startsWith('DESC')){
        const parsedResult = result.map((item) => {
          const parsedItem = JSON.parse(item); // Parse each string into an object
          // return parsedItem.col_name + " " + parsedItem.data_type; // Return the 'namespace' value
          return parsedItem.col_name; // Return the 'namespace' value
        });

        // Return parsed columns or rows
        return parsedResult.map((column) => ({
          name: column,
          children: null
        }));

      }else {
        // Process result as namespaces
        const parsedResult = result.map((item) => {
          const parsedItem = JSON.parse(item); // Parse each string into an object
          return parsedItem.namespace; // Return the 'namespace' value
        });

        const excludedNamespaces = ["information_schema", "pg_catalog", "public"];

        // Filter out any excluded namespaces at the 5th level
        const filteredResult = parsedResult.filter((namespace) => {
          // Check if the current level is 5, and if the namespace contains an excluded item
          if (getCurrentLevel(`${fullPath}.${namespace}`) === 5) {
            return !excludedNamespaces.some((excluded) => namespace.includes(excluded));
          }
          return true; // If not at the 5th level, include the namespace
        });

        // Return the child nodes with their full paths
        return filteredResult.map((namespace) => ({
          name: namespace,
          fullPath: `${fullPath}.${namespace}`, // Keep track of the full path for further queries
          children: null // Initially no children for deeper levels
        }));
      }
    } else {
      return []; // Return an empty array in case of an error
    }
  };

  const getTableDesc = async (fullPath) => {
    let query = `DESC ${fullPath}`;
    const result = await fetchApi(query);

    if (result) {
        const parsedResult = result.map((item) => {
          const parsedItem = JSON.parse(item); // Parse each string into an object
          return parsedItem; // Return the parsed item as a JSON object
        });
        return parsedResult; // Make sure to return the parsed result
    } else {
      return []; // Return an empty array in case of an error
    }
  }

  // Function to fetch the initial data for 'lightning.datasource' and its children (up to 3 levels)
  const fetchInitialData = async (parentNode, depth) => {
    if (depth === 0 || parentNode.children) return; // Stop at the third level or if children already exist

    // Fetch the child namespaces for the current node
    const childNodes = await fetchDatasources(parentNode.fullPath || parentNode.name); // Use full path if available

    // Update the node with its child nodes
    setDataSources((prevData) =>
      updateNodeChildren(prevData, parentNode.name, childNodes)
    );

    // Recursively fetch children up to 3 levels
    for (let child of childNodes) {
      await fetchInitialData(child, depth - 1); // Fetch children for next level
    }
  };    

  // Function to fetch child nodes dynamically on node click
  const fetchChildNodes = async (node) => {
    // Check if the node already has children to avoid redundant fetch
    if (node.children && node.children.length > 0) return; // If already loaded, do nothing

    // Use the full path to construct the query
    const childNodes = await fetchDatasources(node.fullPath || node.name);

    // Update the node with its child nodes
    setDataSources((prevData) =>
      updateNodeChildren(prevData, node.name, childNodes)
    );
  };

  // Function to update a node's children in the tree
  const updateNodeChildren = (nodes, nodeName, children) => {
    return nodes.map((node) => {
      if (node.name === nodeName) {
        return { ...node, children }; // Update the children of the node
      }
      if (node.children) {
        return { ...node, children: updateNodeChildren(node.children, nodeName, children) };
      }
      return node;
    });
  };

  // Example usage of the fetchInitialData to load the initial tree
  // useEffect(() => {
  //   fetchInitialData({ name: 'lightning.datasource', fullPath: 'lightning.datasource' }, 5);
  // }, []);

  // Example usage of the fetchInitialData to load the initial tree
  useEffect(() => {
    fetchInitialData({ name: 'lightning.datasource', fullPath: 'lightning.datasource' }, 5);
  }, [refreshNav]); // Add refreshNav as a dependency to trigger refresh


  // When a table is clicked, the table information is saved in the state.
  const handleTableClick = async (node) => {
    // const currentLevel = getCurrentLevel(node.fullPath || node.name);
    // if (currentLevel === 6 && view === 'semanticLayer') {
    if (node.isTable === true && view === 'semanticLayer') {
      const tableDesc = await getTableDesc(node.fullPath); 
      onTableSelect(node.name, tableDesc);
    }
    fetchChildNodes(node);
  };

  // const renderTree = (nodes) => {
  //   return nodes.map((node) => (
  //     <TreeItem
  //       key={node.name}
  //       itemId={node.name}
  //       label={node.name}
  //       onClick={() => handleTableClick(node)}
  //     >
  //       {Array.isArray(node.children) ? renderTree(node.children) : null}
  //     </TreeItem>
  //   ));
  // };

  const renderTree = (nodes, parentPath = '') => {
    return nodes.map((node, index) => {

      const currentPath = node.fullPath || (parentPath ? `${parentPath}.${node.name}` : node.name);
      
      // const currentLevel = getCurrentLevel(node.fullPath || node.name);

      const uniqueId = `${currentPath}-${index+1}`; 
  
      return (
        <TreeItem
          key={uniqueId}
          itemId={uniqueId}
          label={
            <>
              {node.name}
              {node.isTable === true && view === 'semanticLayer' && (
                <button
                  style={{ marginLeft: '10px', padding: '2px 5px', fontSize: '12px' }}
                  onClick={() => handleTableClick(node)}
                >
                  Add to Table
                </button>
              )}
            </>
          }
          onClick={() => fetchChildNodes(node)}  // Fetch child nodes on click
        >
          {Array.isArray(node.children) ? renderTree(node.children, currentPath) : null}
        </TreeItem>
      );
    });
  };
  

  // Example data for Semantic Layer (for now it's static)
  const exData = [
    {
      name: 'lightning.datasource',
      isFile: false,
      children: [
        {
          name: 'rdbms',
          isFile: false,
          children: [
            {
              name: 'my_postgres',
              isFile: false,
              children: [
                {
                  name: 'nytaxis',
                  isFile: false,
                  children: [
                    {
                      name: 'customer',
                      isFile: false,
                      children: [
                        { name: 'customer_id', isFile: true },
                        { name: 'name', isFile: true },
                        { name: 'address', isFile: true }
                      ]
                    },
                    {
                      name: 'orders',
                      isFile: false,
                      children: [
                        { name: 'order_id', isFile: true },
                        { name: 'order_date', isFile: true },
                        { name: 'total', isFile: true }
                      ]
                    }
                  ]
                }
              ]
            }
          ]
        }
      ]
    }
  ];

  return (
    <div className='guideline'>
      {/* Data Sources Tree */}
      <div className='nav-tab bold-text'>Data Sources</div>
      <SimpleTreeView className='tree-view'>
        {renderTree(dataSources)}
      </SimpleTreeView>

      <div className='separator'></div>

      {/* Semantic Layer (Static Example Data) */}
      <div className='nav-tab bold-text'>Semantic Layer</div>
      <SimpleTreeView className='tree-view'>
        {renderTree(exData)}
      </SimpleTreeView>
    </div>
  );
};

export default Navigation;
