import ace from 'ace-builds';

// Define custom theme
export const defineCustomTheme = () => {
  ace.define('ace/theme/myCustomTheme', ['require', 'exports', 'module', 'ace/lib/dom'], function (require, exports) {
    exports.isDark = false;
    exports.cssClass = 'ace-myCustomTheme';
    exports.cssText = `
      .ace-myCustomTheme .ace_gutter {
        background: #FFFFFF;
        color: #27A7D2;
      }
      .ace-myCustomTheme .ace_print-margin {
        display: none;
      }
      .ace-myCustomTheme {
        background-color: #FFFFFF;
        color: #27A7D2;
      }
      .ace-myCustomTheme .ace_cursor {
        color: #134B70;
      }
      .ace-myCustomTheme .ace_marker-layer .ace_selection {
        background: #CECECE;
      }
      .ace-myCustomTheme .ace_keyword {
        color: #134B70;
        font-weight: bold;
      }
      .ace-myCustomTheme .ace_comment {
        color: #606676;
        font-style: italic;
      }
      .ace-myCustomTheme .ace_line-number {
        color: #27A7D2;
      }
    `;
    var dom = require('ace/lib/dom');
    dom.importCssString(exports.cssText, exports.cssClass);
  });
};

// SQL Autocompletion
export const customSQLCompleter = {
  getCompletions: (editor, session, pos, prefix, callback) => {
    const sqlKeywords = [
      { name: 'SELECT', value: 'SELECT', score: 1000, meta: 'keyword' },
      { name: 'FROM', value: 'FROM', score: 1000, meta: 'keyword' },
      // Add more SQL keywords as needed
    ];
    callback(null, sqlKeywords.map((keyword) => ({
      caption: keyword.name,
      value: keyword.value,
      meta: keyword.meta,
      score: keyword.score,
    })));
  },
};

// Add completer to Ace editor
export const setupAceEditor = () => {
  ace.require('ace/ext/language_tools').addCompleter(customSQLCompleter);
};

// Editor default options
export const editorOptions = {
  enableBasicAutocompletion: true,
  enableLiveAutocompletion: true,
  enableSnippets: false,
  showLineNumbers: true,
  tabSize: 2,
};
