import { jsPlumb } from 'jsplumb';
import './JsPlumbTables.css';

// Initialize jsPlumb instance and set up event bindings
export const initializeJsPlumb = (container, tables) => {
  const jsPlumbInstance = jsPlumb.getInstance({
    Container: container,
  });

  jsPlumbInstance.ready(() => {
    tables.forEach((table) => {
      setupTable(container, table, jsPlumbInstance);
    });

    jsPlumbInstance.repaintEverything(); // Redraw all elements

    // Add event handler to delete connections when clicked
    jsPlumbInstance.bind('click', (connection, originalEvent) => {
      jsPlumbInstance.deleteConnection(connection); // delete connection
    });

    // Handle connection events and prevent connections within the same table
    jsPlumbInstance.bind('beforeDrop', (info) => {
      console.log("info : ", info)
      const sourceId = info.sourceId;
      const targetId = info.targetId;

      const sourceTable = sourceId.split('-row-')[0];
      const targetTable = targetId.split('-row-')[0];

      // Prevent connections within the same table
      if (sourceTable === targetTable) {
        alert('Cannot connect within the same table.');
        return false; // Cancel the connection
      }

      return true;
    });

    // Handle connection detached events
    jsPlumbInstance.bind('connectionDetached', (info) => {
      console.log('Connection detached:', info);
    });

    // Handle connection moved events
    jsPlumbInstance.bind('connectionMoved', (info) => {
      console.log('Connection moved:', info);
    });
  });

  return jsPlumbInstance;
};

// Setup a single table and make it draggable
export const setupTable = (container, table, jsPlumbInstance) => {
  const tableContainer = document.createElement('div');
  tableContainer.style.position = 'absolute';
  tableContainer.style.top = table.top;
  tableContainer.style.left = table.left;
  tableContainer.style.width = '220px'; // Set width for the container
  tableContainer.style.marginBottom = '20px'; // Add some margin below the container
  tableContainer.id = table.id; // Set an ID for the tableContainer

  // Create the table element
  const tableElement = document.createElement('table');
  tableElement.style.width = '100%';
  tableElement.style.borderCollapse = 'collapse';
  tableElement.style.border = '1px solid #ddd';

  // Add the caption for the table title (this ensures the title is centered)
  const caption = document.createElement('caption');
  caption.innerText = table.title;
  caption.style.padding = '10px';
  caption.style.textAlign = 'center';
  caption.style.fontSize = '16px';
  caption.style.fontWeight = 'bold';
  tableElement.appendChild(caption);

  const tbody = document.createElement('tbody');
  tableElement.appendChild(tbody);
  
  tableContainer.appendChild(tableElement);
  container.appendChild(tableContainer); // Append the container, not just the table

  // Set the table container as draggable
  setTimeout(() => {
    if (jsPlumbInstance && tableContainer) {
      makeTableDraggable(tableContainer, jsPlumbInstance, container); // Make the table container draggable
    } else {
      console.error("jsPlumbInstance was not initialized or tableContainer is missing.");
    }
  }, 100);

  // Add button for adding rows
  const buttonContainer = document.createElement('div');
  buttonContainer.style.textAlign = 'center'; // Center the button
  buttonContainer.style.marginTop = '10px'; // Add some margin from the table
  tableContainer.appendChild(buttonContainer);

  addRowButton(table.id, jsPlumbInstance, buttonContainer); // Pass the button container
};

export const setupTableForSelectedTable = (container, selectedTable, jsPlumbInstance) => {
  // console.log("selectedTable : ", selectedTable.name);

  // Create the container with new style
  const tableContainer = document.createElement('div');
  tableContainer.className = 'table-container'; // Add CSS class for styling
  tableContainer.style.position = 'absolute';
  tableContainer.style.top = selectedTable.top;
  tableContainer.style.left = selectedTable.left;
  tableContainer.id = selectedTable.name; // Set an ID for the tableContainer

  // Create the table element with styled header
  const tableElement = document.createElement('table');
  tableElement.className = 'table-body';

  // Create table header (caption)
  const caption = document.createElement('div');
  caption.className = 'table-header'; // Add CSS class for styling
  caption.innerText = selectedTable.name;
  tableElement.appendChild(caption);

  const tbody = document.createElement('tbody');
  tableElement.appendChild(tbody);

  // Automatically add rows based on the selectedTable columns
  selectedTable.desc.forEach((column, index) => {
    const row = document.createElement('tr');
    row.className = 'table-row'; // Add CSS class for styling

    // Create table cells for name and type
    const nameCell = document.createElement('td');
    nameCell.className = 'table-cell'; // Add CSS class for styling
    nameCell.innerText = column.col_name;

    const typeCell = document.createElement('td');
    typeCell.className = 'table-cell'; // Add CSS class for styling
    typeCell.innerText = column.data_type;

    row.appendChild(nameCell);
    row.appendChild(typeCell);
    tbody.appendChild(row);

    // Create endpoints for each cell (left for name, right for type)
    const leftEndpointId = `${selectedTable.id}-col-${index + 1}-left`;
    const rightEndpointId = `${selectedTable.id}-col-${index + 1}-right`;

    nameCell.id = leftEndpointId;
    typeCell.id = rightEndpointId;

    // Add endpoints to the left (name) and right (type) cells
    requestAnimationFrame(() => {
      addEndpoint(jsPlumbInstance, leftEndpointId, 'LeftMiddle', getDefaultConnectionConfig());
      addEndpoint(jsPlumbInstance, rightEndpointId, 'RightMiddle', getDefaultConnectionConfig());
    });
  });

  tableContainer.appendChild(tableElement);
  container.appendChild(tableContainer); // Append the container, not just the table

  // Set the table container as draggable
  requestAnimationFrame(() => {
    if (jsPlumbInstance && tableContainer) {
      makeTableDraggable(tableContainer, jsPlumbInstance, container);
    } else {
      console.error("jsPlumbInstance was not initialized or tableContainer is missing.");
    }
  });
};

// Function to add a single row to a table
export const addTableRow = (tableId, jsPlumbInstance) => {
  const tableElement = document.getElementById(tableId).querySelector('tbody');
  const rowCount = tableElement.rows.length + 1; // Increment row count for new row

  // Create a new row with endpoints for left and right
  const row = document.createElement('tr');
  row.style.border = '1px solid #ddd';

  const leftCell = document.createElement('td');
  leftCell.id = `${tableId}-row-${rowCount}-left`;
  leftCell.style.textAlign = 'left'; // Left align for left column
  leftCell.style.padding = '8px';
  leftCell.style.width = '50%';
  leftCell.style.borderRight = '1px solid #ddd';
  leftCell.innerText = `Left ${rowCount}`;

  const rightCell = document.createElement('td');
  rightCell.id = `${tableId}-row-${rowCount}-right`;
  rightCell.style.textAlign = 'right'; // Right align for right column
  rightCell.style.padding = '8px';
  rightCell.style.width = '50%';
  rightCell.innerText = `Right ${rowCount}`;

  row.appendChild(leftCell);
  row.appendChild(rightCell);
  tableElement.appendChild(row);

  // Create endpoints for the newly added row
  createEndpointsForTableRow(tableId, rowCount, jsPlumbInstance);
};

// Button to trigger adding rows
export const addRowButton = (tableId, jsPlumbInstance, buttonContainer) => {
  const button = document.createElement('button');
  button.innerText = 'Add Row';
  button.style.padding = '5px 10px';
  button.style.backgroundColor = '#007bff';
  button.style.color = '#fff';
  button.style.border = 'none';
  button.style.borderRadius = '4px';
  button.style.cursor = 'pointer';

  button.addEventListener('click', () => {
    addTableRow(tableId, jsPlumbInstance); // When clicked, add a new row
  });

  // Append the button to the buttonContainer
  buttonContainer.appendChild(button);
};

// Make a table container draggable
const makeTableDraggable = (tableElement, jsPlumbInstance, container) => {
  try {
    if (tableElement) {
      jsPlumbInstance.draggable(tableElement.id, {
        containment: container, // Ensure that the table is dragged only within the container
      });
    } else {
      console.error("Table container not found for draggable operation.");
    }
  } catch (error) {
    console.error(`Failed to make table container draggable: ${error}`);
  }
};

// Create left and right endpoints for a specific row
const createEndpointsForTableRow = (tableId, rowIndex, jsPlumbInstance) => {
  const defaultConfig = getDefaultConnectionConfig();

  const leftEndpoint = `${tableId}-row-${rowIndex}-left`;
  const rightEndpoint = `${tableId}-row-${rowIndex}-right`;

  const leftElement = document.getElementById(leftEndpoint);
  const rightElement = document.getElementById(rightEndpoint);

  if (leftElement && rightElement) {
    addEndpoint(jsPlumbInstance, leftEndpoint, 'LeftMiddle', defaultConfig);
    addEndpoint(jsPlumbInstance, rightEndpoint, 'RightMiddle', defaultConfig);
    makeSourceAndTarget(jsPlumbInstance, leftEndpoint, rightEndpoint);
  } else {
    console.warn(`Row elements for endpoints ${leftEndpoint} or ${rightEndpoint} are missing.`);
  }
};

// Create endpoints and make them both source and target
const addEndpoint = (jsPlumbInstance, elementId, anchorPosition, config) => {
  jsPlumbInstance.addEndpoint(elementId, {
    anchor: anchorPosition,
    isSource: true,
    isTarget: true,
    maxConnections: -1,
    endpoint: ['Dot', { radius: 4 }],
    ...config,
  });
};

// Configure source and target for the endpoints
const makeSourceAndTarget = (jsPlumbInstance, leftEndpoint, rightEndpoint) => {
  jsPlumbInstance.makeSource(leftEndpoint, {
    filter: '.dot', // Filter is based on points
    filterExclude: false,
    anchor: 'Continuous',
    connectorStyle: { stroke: '#909399', strokeWidth: 2 },
    maxConnections: -1,
    endpoint: 'Dot',
  });

  jsPlumbInstance.makeTarget(rightEndpoint, {
    dropOptions: { hoverClass: 'hover' },
    anchor: 'Continuous',
    allowLoopback: true,
    maxConnections: -1,
    endpoint: 'Dot',
  });
};

// Default configuration for connections
const getDefaultConnectionConfig = () => {
  return {
    connector: ['Bezier', { curviness: 100 }], // Smooth curve
    endpoint: ['Dot', { radius: 6 }], // Larger dot for endpoints
    paintStyle: { stroke: '#007bff', strokeWidth: 2 }, // Blue colored connections
    hoverPaintStyle: { stroke: '#ff0000', strokeWidth: 3 }, // Red when hovered
    overlays: [
      ['Arrow', { width: 12, length: 12, location: 1 }] // More prominent arrow
    ],
    cssClass: 'connection-normal',
    hoverClass: 'connection-hover'
  };
};
